import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AutenticacionPage } from './autenticacion.page';

const routes: Routes = [
  {
    path: 'login',
    component: AutenticacionPage
  },
  {
    path: 'account',
    loadChildren: () => import('../datos-generales-relacion-laboral/datos-generales-relacion-laboral.module')
      .then(m => m.DatosGeneralesRelacionLaboralPageModule)
  },
  {
    path: '**',
    pathMatch: 'full',
    redirectTo: 'login'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AutenticacionPageRoutingModule { }
