import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, from } from 'rxjs';
import { AuthenticationEntity } from '../entities/authentication-entity';
import { ContextService } from './context.service';
import { CapacitorHttp } from '@capacitor/core';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class AfiliadoEmpresaActivoService {
  constructor(private http: HttpClient, private context: ContextService) {}

  get(idEmpresa: string, idTrabajador: string): Observable<any> {
    const url =
      this.context.getEnvUrl().call(null, 'participacion') +
      '/ReporteServicio/autentificar_app' +
      '?nitEmpresa=' +
      idEmpresa +
      '&documentoEmpleado=' +
      idTrabajador;
    return this.http.get(url);
  }

  login(authenticationEntity: AuthenticationEntity): Observable<any> {
    const url = this.context.getEnvUrl().call(null, 'participacion') + '/ReporteServicio/autentificar_app';

    let headers = new HttpHeaders();
    headers = headers.append('Content-Type', 'application/json;charset=UTF-8');
    console.log('url: ', url);

    const response = CapacitorHttp.post({
      url,
      headers: {
        'Content-Type': 'application/json;charset=UTF-8',
      },
      data: authenticationEntity.toJson(),
    }).then(response => {
      console.log(response);
      sessionStorage.setItem('idUsuario', response.data[0].idRegistro);
    }).catch(error => {
      console.log(error);
    });
    //return from(response).pipe(map(response => response.data));

     return this.http.post(url, authenticationEntity.toJson(), { headers });
  }
}
