export class AuthenticationEntity {
  get getNitEmpresa(): string {
    return this._nitEmpresa;
  }

  set setNitEmpresa(nitEmpresa: string) {
    this._nitEmpresa = nitEmpresa;
  }

  get getDocumentoEmpleado(): string {
    return this._documentoEmpleado;
  }

  set setDocumentoEmpleado(documentoEmpleado: string) {
    this._documentoEmpleado = documentoEmpleado;
  }

  get getPass(): string {
    return this._pass;
  }

  set setPass(pass: string) {
    this._pass = pass;
  }

  private _nitEmpresa: string;

  private _documentoEmpleado: string;

  private _pass: string;

  constructor() {}

  toJson(): string {
    return JSON.stringify({
      nitEmpresa: String(this.getNitEmpresa),
      documentoEmpleado: String(this.getDocumentoEmpleado),
      pass: this._pass,
    });
  }
}
