import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FingerprintAIO } from '@ionic-native/fingerprint-aio/ngx';
import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser/ngx';
import { OneSignal } from '@ionic-native/onesignal/ngx';
import { AlertController, LoadingController, Platform } from '@ionic/angular';
import { ID_EMPRESA, ID_TRABAJADOR, NOTIFICACIONES, PAGE_TO_PUSH, USER_PASS } from 'src/app/commons/preferences-keys';
import { alertCommunicationError, presentAlert } from 'src/app/commons/user-feedback';
import { AuthenticationEntity } from 'src/app/entities/authentication-entity';
import { AfiliadoEmpresaActivoService } from 'src/app/services/afiliado-empresa-activo.service';
import { ContextService } from 'src/app/services/context.service';
import { MensajeService } from 'src/app/services/mensaje.service';
import { StorageService } from '../../storage.service';
import { Plugins, Capacitor } from '@capacitor/core';

const { CapacitorHttp } = Plugins;

@Component({
  selector: 'app-autenticacion',
  templateUrl: './autenticacion.page.html',
  styleUrls: ['./autenticacion.page.scss'],
})
export class AutenticacionPage {
  options: InAppBrowserOptions = {
    location: 'yes', //Or 'no'
    hidden: 'no', //Or  'yes'
    clearcache: 'yes',
    clearsessioncache: 'yes',
    zoom: 'yes', //Android only ,shows browser zoom controls
    hardwareback: 'yes',
    mediaPlaybackRequiresUserAction: 'no',
    shouldPauseOnSuspend: 'no', //Android only
    closebuttoncaption: 'Cerrar', //iOS only
    disallowoverscroll: 'no', //iOS only
    toolbar: 'yes', //iOS only
    enableViewportScale: 'no', //iOS only
    allowInlineMediaPlayback: 'no', //iOS only
    presentationstyle: 'fullscreen', //iOS only
    fullscreen: 'yes', //Windows only
  };

  formGroup: FormGroup;

  showPassword = false;

  passwordToggleIcon = 'eye';

  showFinger = false;

  activateFinger = false;

  private authenticationEntity: AuthenticationEntity;

  private idEmpresa: string;

  private submitAttempt: boolean;

  private loading: HTMLIonLoadingElement;

  private mensajeNoAutenticado: string =
    'Señor Trabajador su usuario o contraseña no son correctos. Por favor intente nuevamente si el error persiste comuníquese con el responsable del Sistema de Gestión en Seguridad y Salud en el Trabajo de su empresa para validar acceso al APP';

  constructor(
    private afiliadoEmpresaActivoService: AfiliadoEmpresaActivoService,
    private platform: Platform,
    private alertCtrl: AlertController,
    private formBuilder: FormBuilder,
    private context: ContextService,
    private loadingCtrl: LoadingController,
    private oneSignal: OneSignal,
    private mensajeService: MensajeService,
    private router: Router,
    private iab: InAppBrowser,
    private faio: FingerprintAIO,
    private storageService: StorageService
  ) {
    this.submitAttempt = false;
    this.authenticationEntity = new AuthenticationEntity();

    this.platform.ready().then(() => {

      this.faio
        .isAvailable()
        .then((ava: any) => {
          this.storageService.set('isFingerFaceAvailable', true);
          this.storageService
            .get('showLoginWithFinger')
            .then(result => {
              if (result != null) {
                this.showFinger = result;
              }
            })
            .catch(e => {
              console.log('error: ' + e);
            });
          this.storageService
            .get('activateFinger')
            .then(result => {
              if (result != null) {
                this.activateFinger = result;
              }
            })
            .catch(e => {
              console.log('error: ' + e);
            });
          this.storageService
            .get('autologin')
            .then(result => {
              if (result != null) {
                if (result) {
                  this.autologin();
                } else if (this.showFinger && this.activateFinger && ava == 'face') {
                  this.launchFingerprintModal();
                }
              }
            })
            .catch(e => {
              console.log('error: ' + e);
            });
        })
        .catch((error: any) => {
          console.log(error);
        });
    });

    this.formGroup = this.formBuilder.group({
      cedulaFormControl: ['', Validators.compose([Validators.required])],
      nitFormControl: ['', Validators.compose([Validators.required])],
      passFormControl: ['', Validators.compose([Validators.required])],
    });
  }

  ionViewWillEnter() {
    this.validateShowFinger();
  }

  async presentLoading(): Promise<HTMLIonLoadingElement> {
    const loading = await this.loadingCtrl.create({
      message: 'Autenticando...',
      showBackdrop: true,
      backdropDismiss: false,
    });

    await loading.present();

    return loading;
  }

  async autologin(): Promise<void> {
    this.validateShowFinger();

    const idEmpresa: string = localStorage.getItem(ID_EMPRESA);
    const idTrabajador: string = localStorage.getItem(ID_TRABAJADOR);
    const userPass: string = localStorage.getItem(USER_PASS);

    if (!idEmpresa || !idTrabajador || !userPass) {
      return;
    }

    this.authenticationEntity.setNitEmpresa = idEmpresa;
    this.authenticationEntity.setDocumentoEmpleado = idTrabajador;
    this.authenticationEntity.setPass = userPass;
    const recibirNotificaciones = localStorage.getItem(NOTIFICACIONES) === 'true';
    this.loading = await this.presentLoading();

    this.afiliadoEmpresaActivoService.login(this.authenticationEntity).subscribe(
      (response: any) => {
        console.log(response);
        if (response.length === 0) {
          this.loading.dismiss();
        } else {
          this.oneSignal.setSubscription(recibirNotificaciones);
          this.afterLoginSuccess(response);
        }
      },
      (error: any) => {
        this.loading.dismiss();
        alertCommunicationError(this.alertCtrl);
      }
    );
  }

  validateShowFinger() {
    this.storageService
      .get('showLoginWithFinger')
      .then(result => {
        if (result != null) {
          this.showFinger = result;
        } else {
          this.showFinger = false;
        }
      })
      .catch(e => {
        console.log(e);
        this.showFinger = false;
      });
    this.storageService
      .get('activateFinger')
      .then(result => {
        if (result != null) {
          this.activateFinger = result;
        } else {
          this.activateFinger = false;
        }
      })
      .catch(e => {
        console.log(e);
        this.activateFinger = false;
      });
  }

  async login(): Promise<void> {
    this.submitAttempt = true;
    if (!this.formGroup.valid) {
      presentAlert('Alerta', '', 'Los campos señalados son requeridos', this.alertCtrl);
      this.cleanInputs();
      return;
    }

    this.getLoginData();

    this.loading = await this.presentLoading();
    this.disableCertificateValidation();

    this.afiliadoEmpresaActivoService.login(this.authenticationEntity).subscribe(
      (response: any) => {
        console.log(response);
        if (response.length === 0) {
          this.loading.dismiss();
          presentAlert('Alerta', '', this.mensajeNoAutenticado, this.alertCtrl);
          this.submitAttempt = false;
          this.formGroup.markAsPristine();
          this.cleanInputs();
        } else {
          this.oneSignal.setSubscription(true);
          localStorage.setItem(NOTIFICACIONES, 'true');
          this.storageService.set('isLoginWithFinger', false);
          this.storageService.set('valueOne', this.authenticationEntity.getNitEmpresa);
          this.storageService.set('valueTwo', this.authenticationEntity.getDocumentoEmpleado);
          this.storageService.set('valueThree', this.authenticationEntity.getPass);
          this.afterLoginSuccess(response);
        }
      },
      (error: any) => {
        this.loading.dismiss();
        alertCommunicationError(this.alertCtrl);
      }
    );
  }

  async loginByFinger(): Promise<void> {
    this.submitAttempt = true;
    this.getLoginFingerData();
    this.loading = await this.presentLoading();
    this.afiliadoEmpresaActivoService.login(this.authenticationEntity).subscribe(
      (response: any) => {
        if (response.length === 0) {
          this.loading.dismiss();
          presentAlert('Alerta', '', this.mensajeNoAutenticado, this.alertCtrl);
          this.submitAttempt = false;
          this.formGroup.markAsPristine();
          this.cleanInputs();
        } else {
          this.oneSignal.setSubscription(true);
          localStorage.setItem(NOTIFICACIONES, 'true');
          this.storageService.set('isLoginWithFinger', true);
          this.afterLoginSuccess(response);
        }
      },
      (error: any) => {
        this.loading.dismiss();
        alertCommunicationError(this.alertCtrl);
      }
    );
  }

  launchFingerprintModal() {
    this.faio
      .isAvailable()
      .then((result: any) => {
        console.log(result);
        this.faio
          .show({
            cancelButtonTitle: 'Cancelar',
            //description: "Descripcion",
            disableBackup: true,
            title: 'INGRESAR CON HUELLA',
            //fallbackButtonTitle: 'FB Back Button',
            //subtitle: 'Subtitulo'
          })
          .then((result: any) => {
            console.log(result);
            this.loginByFinger();
          })
          .catch((error: any) => {
            console.log(error);
            this.alertErrorLoginFinger();
          });
      })
      .catch((error: any) => {
        console.log(error);
      });
  }

  afterLoginSuccess(result: any): void {
    this.loading.dismiss();
    this.context.set(result);
    this.storeLogin();
    this.idEmpresa = localStorage.getItem(ID_EMPRESA);

    this.oneSignal
      .getIds()
      .then(ids => {
        this.mensajeService.updateOneSignalPlayerId(ids.userId).subscribe(
          () => {},
          () => alertCommunicationError(this.alertCtrl)
        );
      })
      .catch(error => console.log('Error: ' + JSON.stringify(error)));

    this.pushPage();
  }

  storeLogin(): void {
    localStorage.setItem(ID_EMPRESA, this.authenticationEntity.getNitEmpresa);
    localStorage.setItem(ID_TRABAJADOR, this.authenticationEntity.getDocumentoEmpleado);
    localStorage.setItem(USER_PASS, this.authenticationEntity.getPass);
  }

  pushPage(): void {
    const page = localStorage.getItem(PAGE_TO_PUSH) || '/home';
    localStorage.removeItem(PAGE_TO_PUSH);
    this.router.navigate([page]);
  }

  isCedulaInvalid(): boolean {
    return !this.formGroup.controls.cedulaFormControl.valid && this.submitAttempt;
  }

  isNitInvalid(): boolean {
    return !this.formGroup.controls.nitFormControl.valid && this.submitAttempt;
  }

  isPasswordValid(): boolean {
    return !this.formGroup.controls.passFormControl.valid && this.submitAttempt;
  }

  cleanInputs(): void {
    this.formGroup.controls.cedulaFormControl.setValue('');
    this.formGroup.controls.nitFormControl.setValue('');
    this.formGroup.controls.passFormControl.setValue('');
  }

  getLoginData(): void {
    this.authenticationEntity.setNitEmpresa = this.formGroup.controls.nitFormControl.value;
    this.authenticationEntity.setDocumentoEmpleado = this.formGroup.controls.cedulaFormControl.value;
    this.authenticationEntity.setPass = this.formGroup.controls.passFormControl.value;
  }

  getLoginFingerData(): void {
    this.storageService
      .get('valueOne')
      .then(result => {
        if (result != null) {
          this.authenticationEntity.setNitEmpresa = result;
        }
      })
      .catch(e => {
        console.log('error: ' + e);
        alert('¡Lo sentimos! No pudimos iniciar sesión');
      });
    this.storageService
      .get('valueTwo')
      .then(result => {
        if (result != null) {
          this.authenticationEntity.setDocumentoEmpleado = result;
        }
      })
      .catch(e => {
        console.log('error: ' + e);
        alert('¡Lo sentimos! No pudimos iniciar sesión');
      });
    this.storageService
      .get('valueThree')
      .then(result => {
        if (result != null) {
          this.authenticationEntity.setPass = result;
        }
      })
      .catch(e => {
        console.log('error: ' + e);
        alert('¡Lo sentimos! No pudimos iniciar sesión');
      });
  }

  openGetUserUrl() {
    let url: string = this.context.getEnvUrl().call(null, 'positiva');

    if (url.indexOf('-test') > -1) {
      url = 'http://test-positiva.adacsc.co/AdminUsuarios/RegistrarSolicitudUsuarioApp';
    } else {
      url = 'https://alissta.gov.co/AdminUsuarios/RegistrarSolicitudUsuarioApp';
    }

    this.iab.create(url, '_blank', this.options);
  }

  openRestorePasswordUrl() {
    let url = this.context.getEnvUrl().call(null, 'positiva');

    if (url.indexOf('-test') > -1) {
      url = 'http://test-positiva.adacsc.co/AdminUsuarios/RecuperarClaveUsuarioApp';
    } else {
      url = 'https://alissta.gov.co/AdminUsuarios/RecuperarClaveUsuarioApp';
    }

    this.iab.create(url, '_blank', this.options);
  }

  togglePassword(): void {
    this.showPassword = !this.showPassword;
    if (this.passwordToggleIcon === 'eye') {
      this.passwordToggleIcon = 'eye-off';
    } else {
      this.passwordToggleIcon = 'eye';
    }
  }

  async alertErrorLoginFinger() {
    const alert = await this.alertCtrl.create({
      message: 'Intenta nuevamente por favor',
      buttons: [
        {
          text: 'Ok',
        },
      ],
    });
    await alert.present();
  }

  async disableCertificateValidation() {
    if (Capacitor.platform === 'android') {
      try {
        await CapacitorHttp.setServerTrustMode({ mode: 'nocheck' });
        console.log('AVT=>Validación de certificado deshabilitada con éxito en Android');
      } catch (error) {
        console.error('AVT=>Error al deshabilitar la validación de certificado en Android:', error);
      }
    } else {
      console.warn('AVT=>La desactivación de la validación del certificado no es compatible con esta plataforma');
    }
  }
}
